.product_card{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 40px 0;
}
.N-productBox{
    position: relative;
    width: 320px;
    height: 560px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px 30px;
    transition: 0.5s;
    cursor: pointer;
}
.N-productBox::before{
    content: '';
    position: absolute;
    top: 0;
    left: 50px;
    width: 50%;
    height: 100%;
    background: #fff;
    border-radius: 8px;
    -webkit-transform: skewX(15deg);
            transform: skewX(15deg);
    transition: 0.5s;
}
.N-productBox::after{
    content: '';
    position: absolute;
    top: 0;
    left: 50px;
    width: 50%;
    height: 100%;
    background: #fff;
    border-radius: 8px;
    -webkit-transform: skewX(15deg);
            transform: skewX(15deg);
    transition: 0.5s;
    -webkit-filter: blur(30px);
            filter: blur(30px);
    transition: 0.5s;
}
.N-productBox:hover::before,
.N-productBox:hover::after{
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
    left: 20px;
    width: calc(100% - 90px);
}
.N-productBox:nth-child(1)::before,
.N-productBox:nth-child(1)::after{
    background: linear-gradient(315deg,#058f73,#c30849);
}

.N-product-Content{
    position: relative;
    left: 0;
    padding: 20px 40px;
    background: rgba(255,255,255,0.05);
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    border-radius: 8px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    z-index: 1;
    transition: 0.5s;
    color: #fff;
}
.N-productBox:hover .N-product-Content{
    left: -25px;
    padding: 60px 40px;
}

.product_card img{
    width: 80%;
    height: 250px;
    display: block;
    object-fit: cover;
    border-radius: 20px;
    opacity: 0.8;
}
.N-productBox span{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 50;
    pointer-events: none;
}
.N-productBox span::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background: rgba(255,255,255,0.1);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    -webkit-animation: animation 2s ease-in-out infinite;
            animation: animation 2s ease-in-out infinite;
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    
}
.N-productBox:hover span::before{
    top: -50px;
    left: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

.N-productBox span::after{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background: rgba(255,255,255,0.1);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    -webkit-animation: animation 2s ease-in-out infinite;
            animation: animation 2s ease-in-out infinite;
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
    
}
.N-productBox:hover span::after{
    bottom: -50px;
    right: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

@-webkit-keyframes animation
{
    0%,100%
    {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    50%
    {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

@keyframes animation
{
    0%,100%
    {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    50%
    {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

