}
.L-section{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 2px;
    gap: 2px;
    flex-wrap: wrap;
    overflow: hidden;
}
.L-section::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(#000,#f9004d,#000);
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
}
@-webkit-keyframes animate{
    0%
    {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100%
    {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
}
@keyframes animate{
    0%
    {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100%
    {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
}

.L-section span{
    position: relative;
    display: block;
    width: calc(6.25vw - 2px);
    height: calc(6.25vw - 2px);
    background: #0a0a18;
    z-index: 2;
    transition: 1.5s;
}
.L-section span:hover{
