    width: 95%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px;
    margin-top:  80px;
}
.About-Card-container .About-card{
    position: relative;
    max-width: 300px;
    height: 220px;
    background: rgba(255, 255, 255, 0.604);
    margin: 40px 20px;
    padding: 15px 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 5px 102px rgba(131, 120, 120, 0.5);
    transition: 0.3s ease-in-out;
    border-radius: 10px;
    border: 1px solid rgba(66, 63, 63, 0.526);
    border-top: 1px solid rgba(255, 255, 255, 0.526);
    border-left: 1px solid rgba(255, 255, 255, 0.547);
}
.About-Card-container .About-card:hover{
    height: 440px;
}
.About-Card-container .About-card .About-imgbx{
    position: relative;
    width: 260px;
    height: 260px;
    top: -100px;
    left: 5px;
    z-index: 1;
    box-shadow: 0 5px 20px rgba(0,0,0,0.3);
    border: 1px solid rgba(66, 63, 63, 0.526);
    border-top: 1px solid rgba(255, 255, 255, 0.526);
    border-left: 1px solid rgba(255, 255, 255, 0.547);
    border-radius: 8px;
}
.About-Card-container .About-card .About-imgbx img{
    max-width: 100%;
    border-radius: 8px;
    opacity: 0.9;
}
.About-Card-container .About-card .About-card-content{
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: -140px;
    padding: 15px 35px;
    text-align: center;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s ease-in-out;
}
.About-Card-container .About-card:hover .About-card-content{
    visibility: visible;
    opacity: 1;
    margin-top: -100px;
    transition-delay: 0.3s;
}
.About-card-content h3{
    font-size: 2em;
    color: #342136;
    font-weight: bold;
    margin-bottom: 10px;
    text-shadow: 0 5px 20px rgba(58, 55, 55, 0.663);
}
.About-card-content p{
    position: relative;
    font-size: 16px;
    color: #2a2c33;
    font-weight: bold;
    margin-bottom: 10px;
}
.myProgressBar{
    min-height: 40vh;
    background: #161922;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.myProgressBar-wrapper{
    position: relative;
    width: 90vw;
    display: flex;
    justify-content: space-around;
    grid-gap: 25px;
    gap: 25px;
}
.myProgressBar-container{
    width: 28vmin;
    height: 28vmin;
    display: flex;
    flex-direction: column;
    color: #fff;
    justify-content: space-around;
    padding: 1em 0;
    position: relative;
    font-size: 16px;
    border-radius: .5em;
    background: #21242b;
    border-bottom: 10px solid #18f98f;
    border-top: 1px solid rgba(255, 255, 255, 0.359);
    border-left: 1px solid rgba(255, 255, 255, 0.359);
    overflow: hidden;
    -webkit-box-reflect: below 1px linear-gradient(transparent,rgba(0, 0, 0, 0.047));
    transition: .4s;
}
.myProgressBar-container:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.myProgressBar-container::before{
    content: '';
    position: absolute;
    top: 0;
    left: -50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.047);
    -webkit-transform: skew(25deg);
            transform: skew(25deg);
}
.myProgressBar-container i{
    color: #18f98f;
    font-size: 2.5em;
    text-align: center;
}
.myProgressBar-container span.upnums{
    display: grid;
    place-items: center;
    font-weight: 600;
    font-size: 3em;
}
.myProgressBar-container span.upnumsText{
    color: rgb(205, 199, 199);
    font-size: 1.2em;
    text-align: center;
    pad: 0.7em 0;
    font-weight: 400;
    line-height: 0;
}

@media screen and (max-width: 1024px){
    .myProgressBar-wrapper{
        width: 80vw;
    }
    .myProgressBar-container{
        height: 30vmin;
        width: 30vmin;
        font-size: 14px;
    }
}
@media screen and (max-width: 768px){
    .myProgressBar-wrapper{
        width: 90vw;
        flex-wrap: wrap;
        grid-gap: 30px;
        gap: 30px;
    }
    .myProgressBar-container{
        height: 26vmin;
        width: calc(50% -40%);
        font-size: 14px;
    }
}
@media screen and (max-width: 480px){
    .myProgressBar-wrapper{
        grid-gap: 15px;
        gap: 15px;
    }
    .myProgressBar-container{
        height: 25vmin;
        width: 80%;
        font-size: 8px;
    }
}
