.Se-Lightbar{
    position: absolute;
    top: 40px;
    left: 0;
    width: 8px;
    height: 42%;
    border-radius: 10px;
    background: #fff;
    z-index: 2;
    box-shadow: 0 0 10px #2bfcb6,
                0 0 40px #2bfcb6;
    -webkit-animation: animatelightbar 5s linear infinite;
            animation: animatelightbar 5s linear infinite;
}
@-webkit-keyframes animatelightbar{
    0%,5%{
        -webkit-transform: scaleY(0) translateX(0);
                transform: scaleY(0) translateX(0);
    }
    10%{
        -webkit-transform: scaleY(1) translateX(0);
                transform: scaleY(1) translateX(0);
    }
    90%{
        -webkit-transform: scaleY(1) translateX(calc(900px - 7px));
                transform: scaleY(1) translateX(calc(900px - 7px));
    }
    95%,100%{
        -webkit-transform: scaleY(0) translateX(calc(900px - 7px));
                transform: scaleY(0) translateX(calc(900px - 7px));
    }
}
@keyframes animatelightbar{
    0%,5%{
        -webkit-transform: scaleY(0) translateX(0);
                transform: scaleY(0) translateX(0);
    }
    10%{
        -webkit-transform: scaleY(1) translateX(0);
                transform: scaleY(1) translateX(0);
    }
    90%{
        -webkit-transform: scaleY(1) translateX(calc(900px - 7px));
                transform: scaleY(1) translateX(calc(900px - 7px));
    }
    95%,100%{
        -webkit-transform: scaleY(0) translateX(calc(900px - 7px));
                transform: scaleY(0) translateX(calc(900px - 7px));
    }
}
.Se-toplayer{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90%;
