}
@-webkit-keyframes animatetoplayer{
    0%,2.5%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    5%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    45%{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    47.5%,50%{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }



    50.001%,52.5%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    55%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    95%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    97.5%,100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@keyframes animatetoplayer{
    0%,2.5%{
        -webkit-transform: translateX(0);
