.sinfo{
    position: relative;
    width: 670px;
    max-width: 100%;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.5);
    overflow: hidden;
    border-radius: 20px;
    transition: .4s;
    cursor: pointer;
    box-shadow: 0 15px 100px rgba(0, 0, 0, 0.451);
}
.sinfo:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.sinfo::before{
    content: '';
    position: absolute;
    width: 150px;
    height: 750px;
    background: linear-gradient(#00ffc8,#ff16cd);
    -webkit-animation: rotateboder 4s linear infinite;
            animation: rotateboder 4s linear infinite;
}
@-webkit-keyframes rotateboder{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(350deg);
                transform: rotate(350deg);
    }
}
@keyframes rotateboder{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(350deg);
                transform: rotate(350deg);
    }
}
.sinfo::after{
    content: '';
    position: absolute;
    background: #101322;
    inset: 4px;
    border-radius: 16px;
}
.sinfo p{
